@mixin scrollbar(){
    &::-webkit-scrollbar {
        width: 14px;
        height: 18px;
    }
    &::-webkit-scrollbar-thumb {
        height: 6px;
        border: 4px solid rgba(0, 0, 0, 0);
        background-clip: padding-box;
        border-radius: 7px;
        background-color: rgba(0, 0, 0, 0.55);
        box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.05), inset 1px 1px 0px rgba(0, 0, 0, 0.05);
    }
    &::-webkit-scrollbar-button {
        width: 0;
        height: 0;
        display: none;
    }
    &::-webkit-scrollbar-corner {
        background-color: transparent;
    }
}

.About {
    display: inline-block;
    backdrop-filter: blur(30px);
    position: absolute;
    left: 0;
    height: 100%;
    max-width: 40%;
    border-radius: 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    padding: 10px;
    overflow: auto;
    @include scrollbar();

    h1 {
        text-justify: center;
        text-align: center;
        color: white;
        text-shadow: rgb(58, 0, 48) 1px 0 5px;
    }

    p {
        color: aliceblue;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
        font-size: 1.25rem;
        font-weight: 450;
        text-justify: inter-word;
        text-align: justify;
        overflow: hidden;
    }

    p:last-child {
        overflow: visible;
    }

    .Avatar {
        float: left;
        width: 10vw;
        height: 10vw;
        object-fit: fill;
        margin-right: 15px;
        position: relative;
        shape-outside: circle();
        clip-path: circle();

        .AvatarImg {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            transition: opacity 0.5s ease-in-out;
            position: absolute;
            left: 0;

            &.disabled {
                opacity: 0;
            }
        }
    }

    .Stack {
        width: 100%;

        .StackIcon {
            height: 30px;
            margin: 5px;
            filter: drop-sbetter hadow(0px 5px 3px rgba(0, 0, 0, 0.75));
        }
    }
}

@media (max-width: 900px) {
    .About {
        max-width: 80%;
        height: 100%;
        overflow: auto;
    }
}