@mixin scrollbar(){
    &::-webkit-scrollbar {
        width: 14px;
        height: 18px;
    }
    &::-webkit-scrollbar-thumb {
        height: 6px;
        border: 4px solid rgba(0, 0, 0, 0);
        background-clip: padding-box;
        border-radius: 7px;
        background-color: rgba(0, 0, 0, 0.55);
        box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.05), inset 1px 1px 0px rgba(0, 0, 0, 0.05);
    }
    &::-webkit-scrollbar-button {
        width: 0;
        height: 0;
        display: none;
    }
    &::-webkit-scrollbar-corner {
        background-color: transparent;
    }
}


.ExperienceContainer {
    display: flex;
    justify-content: space-between;
    height: 100%;
    color: white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.Education {
    $item_color: linear-gradient(to left, #3d87b6, #004386c5);
    display: flex;
    width: 33%;
    justify-content: center;
    overflow-y: auto;
    @include scrollbar();
    backdrop-filter: blur(10px);
    border: 1px transparent;
    border-radius: 10px;
    box-shadow: -10px 0 10px 0 rgba(0, 0, 0, 0.5);

    h2 {
        text-justify: center;
        text-align: center;
        color: white;
        text-shadow: rgb(58, 0, 48) 1px 0 5px;

    }

    .Item {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: $item_color;
        border-color: gray;
        border-style:groove;
        border-width: 2px;
        border-radius: 5px;
        margin: 10px;
        padding-left: 10px;
        padding-right: 10px;

    }
}

.Experience {
    $item_color: linear-gradient(to right, #3d87b6, #004386c5);
    width: 66%;
    justify-content: center;
    overflow-y: auto;
    @include scrollbar();
    backdrop-filter: blur(10px);
    border: 1px transparent;
    border-radius: 10px;
    box-shadow: 10px 0 10px 0 rgba(0, 0, 0, 0.5);

    .PDFDownload {
        display: flex;
        position: absolute;
        justify-content: flex-end;
        width: 100%;
        margin-top: 0;

        img {
            width: 25px;
            height: 30px;
            margin: 10px;
        }
    }

    h2 {
        text-justify: center;
        text-align: center;
        color: white;
        text-shadow: rgb(58, 0, 48) 1px 0 5px;
    }

    .Item {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: $item_color;
        border-color: gray;
        border-style: groove;
        border-width: 2px;
        border-radius: 5px;
        margin: 10px;
        padding-left: 10px;
        padding-top: 10px;

        .Inline {
            display: flex;
            flex-direction: row;
            width: 100%;
            height: 100%;

        }
        
        .Date {
            display: flex;
            flex-direction: column;
            width: 100%;
            margin-right: 15px;
            align-self: center;
            font-weight: bold;
        }

        .Company{
            display: flex;
            flex-direction: column;
            max-width: 25%;
            margin-right: 15px;

            img {
                background-color: white;
                border: 1px solid rgb(61, 61, 61);
                border-radius: 5px;
                float: left;
                width:  100px;
                height: 100px;
                object-fit: fill;
            }
        }

        .Description {
            width: 100%;
            height: 100%;
            margin-right: 15px;
            text-align: justify;
            text-justify: inter-word;
            text-wrap: normal;
        }

        .Skills {
            display: flex;
            justify-content: flex-end;
            align-items: bottom;
            width: 100%;
            height: 100%;
            padding-bottom: 5px;
            flex-wrap: wrap;

            .Skill {
                display: inline;
                list-style-type: none;
                margin: 1px 3px;
                padding: 1px 5px;
                border-radius: 7px;
                background-color: #8d0042;
                font-size: 0.75rem;
                font-weight: bold;
                color: white;
            }
        }
    }
}

@media (max-width: 768px) {
    .ExperienceContainer {
        flex-direction: column;
        overflow-y: auto;
        @include scrollbar();
    }

    .Education {
        height: 100%;
        width: 100%;
        margin: 10px 0;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
        font-size: small;
    }
    .Experience {
        height: 100%;
        width: 100%;
        margin: 10px 0;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
        font-size: small;

        .Item {
            .Description {
                p {
                    padding-top: 20px;
                }
                h4 {
                    text-align: end;
                    margin-right: 2px;
                }

                .Date {
                    text-align: end;
                }
            }
        }
    }
}