.ContentContainer {
    height: 100vh;
    width: 100vw;

    .Content {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: 10px;
    }

    .ContentCard {
        position: absolute;
        padding: 10px;
        margin: 40px 20px 20px 20px;
        height: 80vh;
        width: 90vw;
        border-radius: 15px;

        &.active {
            transition: transform 0.5s ease-out;
            transform: translateX(0);

            .About {
                transition: transform 0.5s ease-out;
                transform: translateX(0);
            }
        }

        &.hidden {
            transition: transform 0.5s ease-out;
            transform: translateX(100vw);

            .About {
                transition: transform 0.5s ease-out;
                transform: translateX(-200vw);
            }
        }
    }
}