$bg-color-hover: #000;
$font-color: #ffffff;

.NavbarContainer {
    display: flex;
    position: fixed;
    pointer-events: none;
    width: 100%;
    margin-top: 0;
    justify-content: center;

    .Navbar {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        backdrop-filter: blur(10px);
        padding: 5px 50px;
        border-radius: 0 0 15px 15px;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    }

    .NavbarItem {
        padding: 2px 15px;
        margin: 2px;
        border-radius: 5px;
        background: linear-gradient(
            180deg,
            rgba(0, 0, 0, 0.75) 0%,
            rgba(0, 0, 0, 0) 100%
        );
        background-size: 100% 200%;
        background-position: 100% -100%;
        color: $font-color;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
        font-weight: bold;
        pointer-events: all;
        width: 100px;
        transition: all 0.3s ease-in-out;

        &:hover {
            background-position: 100% 0%;
            color: $font-color;
            cursor: pointer;
        }

        &.selected {
            width: 100px;
            background-color: $bg-color-hover;
            color: $font-color;
            pointer-events: none;
        }
    }
}

