$bg-color: #000000bb;
$font-color: #ffffff;

.Footer {
    background-color: $bg-color;
    color: $font-color;
    font-size: 14px;
    font-weight: 300;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    width: 100vw;

    padding-left: 20px;

    a {
        color: $font-color;
    }

    .LanguageSelect {
        padding-right: 10px;
        display: flex;
    }

    .LanguageSelectItem {
        padding: 2px 10px;

        &:hover {
            cursor: pointer;
        }

        &.selected {
            font-weight: bold;
        }
    }

}