.ContactContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    align-items: center;
}

.Contact {
    display: flex;
    flex-direction: column;
    
    backdrop-filter: blur(10px);
    border: 1px transparent;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    width: 55%;
    margin-right: 10%;

    h1 {
        text-justify: center;
        text-align: center;
        color: white;
        text-shadow: rgb(58, 0, 48) 1px 0 5px;
    }

    ul {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 6%;
        font-size: 1.5rem;
        font-weight: 450;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
        color: #fff;
        text-align: center;
        letter-spacing: 0.1rem;
        padding: 50px 0;

        a {
            transition: color 0.3s ease;
            text-decoration: none;
            color: #fff;
            text-shadow: -1px -1px 0 rgb(151, 8, 108), 1px -1px 0 rgb(151, 8, 108), -1px 1px 0 rgb(151, 8, 108), 1px 1px 0 rgb(151, 8, 108);
            &:hover {
                color: rgb(19, 0, 5);
                text-shadow: white 5px 0 5px;
            }
        }
    }
    
    li {
        display: flex;
        align-items: center;
        justify-content: left;
    }
}

@media (max-width: 768px) {
    .Contact {
        width: 90%;
        font-size: 1.15rem;
    }
}